//Sources: Fixing shader error: https://github.com/parcel-bundler/parcel/issues/928
// Fixing error: https://stackoverflow.com/questions/71279498/phaser-3-images-not-showing
// Fixing other errro: https://stackoverflow.com/questions/71225698/phaser-3-spritesheet-doesnt-load-correctly
//import platform from './assets/platform2.png';
//import pineapple from './assets/pineapple.png';
let game; 

const gameOptions = {
  playerGravity: 800,
  playerSpeed: 300
};

window.onload = function() {
  let gameConfig = {
    type: Phaser.AUTO,
    background: "#454545",
    backgroundColor: "#323232",
    scale: {
      mode: Phaser.Scale.Fit,
      autoCenter: Phaser.Scale.CENTER_BOTH,
      width: 800,
      height: 1000,
    },
    pixelArt: true,
    physics: {
      default: "arcade",
      arcade: {
        gravity: {
          y: 0
        }
      }
    },
    scene: PlayGame
  };
  game = new Phaser.Game(gameConfig);
  window.focus();
}


class PlayGame extends Phaser.Scene {
  constructor() {
    super("PlayGame")
  }
  preload() {
    //this.load.background("background", "assets/background.png");
    this.load.image("platform", "./assets/platform2.png");
    this.load.image("popsicle", "./assets/popsicle.png");
    this.load.spritesheet("player", "./assets/player.png", {
      frameWidth: 32,
      frameHeight: 48,
    }
    );
  
  }
  create() {
    //The platform: 
    this.platformGroup = this.physics.add.group({
      immovable: true,
      allowGravity: false
    });
    this.platformGroup.create(game.config.width/2, game.config.height/2, "platform")
    //Setting places for platforms
    for(let i = 0; i < 20; i++) {
      this.platformGroup.create(Phaser.Math.Between(0, game.config.width), Phaser.Math.Between(0, game.config.height), "platform");
  }

    this.player = this.physics.add.sprite(game.config.width/2, game.config.height/2, "player")
    this.player.body.gravity.y = gameOptions.playerGravity;
    this.physics.add.collider(this.player, this.platformGroup);

    this.popsicleGroup = this.physics.add.group({})
    this.physics.add.collider(this.popsicleGroup, this.platformGroup);
    this.add.image(200, 200, "popsicle")
    this.cursors = this.input.keyboard.createCursorKeys();

    this.anims.create({
        key: "left", 
        frames: this.anims.generateFrameNumbers("player", {start: 0, end: 3}), 
        frameRate: 10,
        repeat: -1
    })

    this.anims.create({
        key: "turn", 
        frames: [{key: "player", frame: 4}],
        frameRate: 10, 
    })

    this.anims.create({
        key: "right", 
        frames: this.anims.generateFrameNumbers("player", {start: 5, end: 9}), 
        frameRate: 10, 
    })
  }

  update() {
      if(this.cursors.left.isDown) {
        this.player.body.velocity.x = -gameOptions.playerSpeed;
        this.player.anims.play("left", true);
    } else if (this.cursors.right.isDown) {
      this.player.body.velocity.x = gameOptions.playerSpeed;
      this.player.anims.play("right", true);
    }
    else   {
      this.player.body.velocity.x=0; 
      this.player.anims.play("turn", true);
    }

    if (this.cursors.up.isDown) {
      this.player.body.velocity.y = -gameOptions.playerGravity/1.6; 
    }


}
}